@supports (display: grid) {
  @media screen and (max-width: 1000px) {
    .response_container {
      display: grid;
      row-gap: 10px;
      column-gap: 10px;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(5, auto);
      grid-template-areas:
        "question_coming question_coming toggle_button"
        "question_number question_number question_number"
        "minus_button number_of_people plus_button"
        "send_button send_button send_button"
        "warning warning warning";
    }

    .response_button_style {
      width: 30vw;
      height: 10vw;
      border-radius: 5vw;
      font-size: 15px;
      background-color: rgba(0, 0, 0, 1);
      border-color: rgba(0, 0, 0, 1);
      color: rgba(255, 255, 255, 1);
      outline: none;
    }

    .response_button_style_round {
      /* width: 10vw;
      height: 10vw; */
      width: 15vw;
      height: 15vw;
      border-radius: 7.5vw;
      background-color: rgba(255, 255, 255, 1);
      border-color: rgba(0, 0, 0, 1);
      border-width: 1.5px;
      font-size: 25px;
      outline: none;
    }

    .response_button_style_round_disabled {
      /* width: 10vw;
      height: 10vw; */
      width: 15vw;
      height: 15vw;
      border-radius: 7.5vw;
      background-color: rgba(112, 106, 108, 0.3);
      border-color: rgba(0, 0, 0, 1);
      border-width: 1.5px;
      font-size: 25px;
      outline: none;
    }

    .response_container > div {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 25px;
    }

    .response_question_photo {
      height: 10vw;
    }
  }
  @media screen and (min-width: 1000px) {
    .response_container {
      display: grid;
      row-gap: 10px;
      column-gap: 10px;
      grid-template-columns: repeat(4, auto);
      grid-template-rows: repeat(4, auto);
      grid-template-areas:
        "question_coming . toggle_button ."
        "question_number minus_button number_of_people plus_button"
        "send_button send_button send_button send_button"
        "warning warning warning warning";
    }

    .response_button_style {
      width: 150px;
      height: 50px;
      border-radius: 25px;
      font-size: 25px;
      background-color: rgba(0, 0, 0, 1);
      border-color: rgba(0, 0, 0, 1);
      color: rgba(255, 255, 255, 1);
      outline: none;
    }

    .response_button_style_round {
      width: 50px;
      height: 50px;
      border-radius: 25px;
      background-color: rgba(255, 255, 255, 1);
      border-color: rgba(0, 0, 0, 1);
      font-size: 30px;
      outline: none;
    }

    .response_button_style_round_disabled {
      width: 50px;
      height: 50px;
      border-radius: 25px;
      background-color: rgba(112, 106, 108, 0.3);
      border-color: rgba(0, 0, 0, 1);
      font-size: 30px;
      outline: none;
    }

    .response_container > div {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 25px;
    }

    .response_question_photo {
      height: 4vw;
    }
  }
}

.animated {
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
  }
  2% {
    -webkit-transform: translateY(-5px);
  }
  4% {
    -webkit-transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}
@keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
  }
  2% {
    -webkit-transform: translateY(-5px);
  }
  4% {
    -webkit-transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}
.bounce {
  -webkit-animation-name: bounce;
  animation-name: bounce;
}

.question_coming {
  grid-area: question_coming;
}
.toggle_button {
  grid-area: toggle_button;
}
.question_number {
  grid-area: question_number;
}
.minus_button {
  grid-area: minus_button;
}
.number_of_people {
  grid-area: number_of_people;
}
.plus_button {
  grid-area: plus_button;
}
.send_button {
  grid-area: send_button;
  margin-top: 20px;
}
.warning {
  grid-area: warning;
}
.warning_text {
  max-width: 50vw;
  font-size: 15px;
}
